import dagre from 'dagre';

const NODE_WIDTH = 50;
const NODE_HEIGHT = 50;

export const generateFlow = (elements) => {
  
  const g = new dagre.graphlib.Graph({ multigraph: true });

  g.setGraph({
    marginx: 50,
    marginy: 50,
  });
  g.setDefaultEdgeLabel(function() { return {}; });

  elements.forEach ((e) =>  {
    g.setNode(e.name, {
      label: e.name,
      width: NODE_WIDTH,
      height: NODE_HEIGHT
    });
    e.links.forEach((i) => {
      g.setEdge(e.name, i.name, e.name.toString, i.weight);
    });
  });

  dagre.layout(g);


  const generatePositions = (number) => {
    let result = [];
    const angleFactor = 2 * Math.PI / number;
    let angle;
    for(let i = 0; i < number; i++) {
      angle = i * angleFactor;
      let x = 400 * Math.cos(angle);
      let y= 400 * Math.sin(angle);
      result.push([x, y]);
    }
    console.log(result);
    return result;
  }

  const pos = generatePositions(g.nodeCount());

  const nodes = g.nodes().map((i) => {
    let n = g.node(i);
    return {
      id: i,
      data: {
        label: i
      },
      draggable: false,
      style:  {
        background: 'white',
        color: '#23C779',
        width: 50,
        height: 50,
        borderRadius: 50 / 2,
        fontFamily: 'Helvetica',
        fontWeight: 'bold',
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      },
      type: 'customNode',
      width: n.width, 
      height: n.height,
      position: { 
        x: pos[i][0] - n.width /2,
        y: pos[i][1]- n.height / 2
      },
    };
  });

  const edges = g.edges().map((e) => ({
    id: `__${e.v}__${e.w}`,
    points: g.edge(e).points,
    source: e.w,
    type: 'straight',
    style: {
        stroke: "#dddddddd",
        strokeWidth: 2,
    },
    animated: false,
    target: e.v,
    label: 'Weight: ' + e.name,
    labelBgPadding: [8, 4],
    labelBgBorderRadius: 5,
    labelBgStyle: { fill: '#dddddddd'},
    labelStyle: { fill: '#110A2E', fontWeight: 700 },
  }));
 
  return [...nodes, ...edges];
};

