import React, { Component } from "react";
import ReactFlow, {
  removeElements,
  addEdge,
  Controls,
  Background,
  isNode,
} from "react-flow-renderer";
import { customNode, highlightEdgeOnSelect, colorNodeSelect } from "./common";
import { generateFlow } from "./Test";
import Slider from "@material-ui/core/Slider";
import Button from "@material-ui/core/Button";
import ReplayIcon from '@material-ui/icons/Replay';
var selectedNodes = {};
var answerNodes = {};
const nodeTypes = {
  customNode: customNode,
};

export default class TSP extends Component {
  onNodeDragStart = (event, node) => console.log("drag start", node);
  onNodeDragStop = (event, node) => console.log("drag stop", node);
  onPaneClick = (event) => console.log("pane click", event);
  onPaneScroll = (event) => console.log("pane scroll", event);
  onPaneContextMenu = (event) => console.log("pane context menu", event);
  onSelectionDrag = (event, nodes) => console.log("selection drag", nodes);
  onSelectionDragStart = (event, nodes) =>
    console.log("selection drag start", nodes);
  onSelectionDragStop = (event, nodes) =>
    console.log("selection drag stop", nodes);
  onSelectionContextMenu = (event, nodes) => {
    event.preventDefault();
    console.log("selection context menu", nodes);
  };

  onMoveEnd = (transform) => console.log("zoom/move end", transform);

  generateAdjacencyMatrix = () => {
    let x = Math.floor(Math.random() * 3 + 5);
    var array = new Array(x);
    for (let i = 0; i < x; i++) {
      array[i] = [];
    }
    for (let i = 0; i < x; i++) {
      for (let j = 0; j < x; j++) {
        let y = Math.floor(Math.random() * 10 + 1);
        if (i !== j) {
          array[i][j] = array[j][i] = y;
        } else {
          array[i][j] = array[j][i] = 0;
        }
      }
    }
    return array;
  };

  makeArray = (w, h, val) => {
    var arr = [];
    for (let i = 0; i < h; i++) {
      arr[i] = [];
      for (let j = 0; j < w; j++) {
        arr[i][j] = val;
      }
    }
    return arr;
  };

  generateLinkNodeRep = (adjMatrix) => {
    let result = [];
    adjMatrix.forEach((arr) => {
      for (let i = 0; i < arr.length; i++) {
        let obj = {
          name: i,
          links: this.getLinks(i, adjMatrix),
        };
        result.push(obj);
      }
    });
    return result;
  };

  getLinks = (index, adjMatrix) => {
    let result = [];
    for (let i = 0; i < adjMatrix[index].length; i++) {
      if (adjMatrix[index][i] !== 0) {
        let link = {
          name: i,
          weight: adjMatrix[index][i],
        };
        result.push(link);
      }
    }
    return result;
  };

  constructor(props) {
    super(props);
    let matrix = this.generateAdjacencyMatrix();
    this.state = {
      matrix: matrix,
      elements: generateFlow(this.generateLinkNodeRep(matrix)),
      delay: 1000,
      running: false,
    };
  }

  replay = () => {
    window.location.reload(false);
  }

  onElementsRemove = (elementsToRemove) =>
    this.setState({
      elements: removeElements(elementsToRemove, this.state.elements),
    });
  onConnect = (params) =>
    this.setState({ elements: addEdge(params, this.state.elemetns) });
  onLoad = (reactFlowInstance) => {
    console.log("flow loaded:", reactFlowInstance);
    reactFlowInstance.fitView();
  };
  onSelectionChange = (elements) => {
    console.log("selection change", elements);
  };

  onElementClick = (event, element) => {
    console.log(`${isNode(element) ? "node" : "edge"} click:`, element);
    if (element != null) {
      if (!selectedNodes.hasOwnProperty(element.id)) {
        selectedNodes[element.id] = 1;
      } else {
        delete selectedNodes[element.id];
      }
      this.setState({
        elements: highlightEdgeOnSelect(this.state.elements, selectedNodes),
      });
    }
  };

  colorNode(start, end) {
    selectedNodes[this.state.elements[start].id] = 1;
    let elem1 = this.state.elements.find((e) => e.id === `__${start}__${end}`);
    let elem2 = this.state.elements.find((e) => e.id === `__${end}__${start}`);
    selectedNodes[elem1.id] = 1;
    selectedNodes[elem2.id] = 1;
    this.setState({
      elements: highlightEdgeOnSelect(this.state.elements, selectedNodes),
      message: `Visiting Node ${end} from Node ${start}`,
    });
  }

  keepNode = (start, end) => {
    answerNodes[this.state.elements[start].id] = 1;
    let elem1 = this.state.elements.find((e) => e.id === `__${start}__${end}`);
    let elem2 = this.state.elements.find((e) => e.id === `__${end}__${start}`);
    answerNodes[elem1.id] = 1;
    answerNodes[elem2.id] = 1;
    this.setState({
      elements: colorNodeSelect(this.state.elements, answerNodes),
    });
  };

  deleteNode(start, end) {
    delete selectedNodes[this.state.elements[start].id];
    let elem1 = this.state.elements.find((e) => e.id === `__${start}__${end}`);
    let elem2 = this.state.elements.find((e) => e.id === `__${end}__${start}`);
    delete selectedNodes[elem1.id];
    delete selectedNodes[elem2.id];
    this.setState({
      elements: highlightEdgeOnSelect(this.state.elements, selectedNodes),
      message: `Unvisiting Node ${end}`,
    });
  }

  async travellingSalesmanProblemHelper(graph) {
    let x = graph.length;
    let memo = this.makeArray(x, Math.pow(2, x), -1);
    let prev = this.makeArray(x, Math.pow(2, x), 0);
    let visited_all = (1 << x) - 1;
    let answer = await this.travellingSalesmanProblem(
      graph,
      prev,
      memo,
      visited_all,
      x,
      1,
      0
    );
    let optimalPath = [];
    let city = 0;
    let position = 1;
    while (true) {
      optimalPath.push(city);
      let nextCity = prev[position][city];
      if (nextCity === 0) break;
      let nextPosition = position | (1 << nextCity);
      position = nextPosition;
      city = nextCity;
    }
    optimalPath.push(0);
    let z = optimalPath[0].toString();
    for (let i = 1; i < optimalPath.length; i++) {
      await this.sleep(this.state.delay / 2);
      this.keepNode(optimalPath[i - 1], optimalPath[i]);
      z = z + " --> " + optimalPath[i].toString();
    }
    this.setState({
      answer: answer,
      path: z,
      message: "Found Path",
    });
    return answer;
  }

  sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  async travellingSalesmanProblem(
    graph,
    prev,
    memo,
    finalState,
    N,
    visitedMask,
    position
  ) {
    if(this.state.running) {
      if (visitedMask === finalState) {
        this.colorNode(position, 0);
        await this.sleep(this.state.delay / 2);
        this.deleteNode(position, 0);
      return graph[position][0];
    } else if (memo[visitedMask][position] !== -1) {
      return memo[visitedMask][position];
    }
    let min = Number.MAX_SAFE_INTEGER;
    let index = -1;
    for (let i = 0; i < N; i++) {
      if ((visitedMask & (1 << i)) === 0) {
        console.log(`Visiting Node ${i} from Node ${position}`);
        this.colorNode(position, i);
        await this.sleep(this.state.delay / 2);
        let ans =
          graph[position][i] +
          (await this.travellingSalesmanProblem(
            graph,
            prev,
            memo,
            finalState,
            N,
            visitedMask | (1 << i),
            i
          ));
        if (ans < min) {
          index = i;
          min = ans;
        }
        console.log(`Unvisiting Node ${i}`);
        await this.sleep(this.state.delay / 2);
        this.deleteNode(position, i);
      }
    }
    prev[visitedMask][position] = index;
    return (memo[visitedMask][position] = min);
    }
  }

  render() {
    return (
      <div style={{ background: "#110A2E", height: window.innerHeight }}>
        <ReactFlow
          elements={this.state.elements}
          onElementClick={this.onElementClick}
          onElementsRemove={this.onElementsRemove}
          onConnect={this.onConnect}
          onPaneClick={this.onPaneClick}
          paneMoveable={false}
          onPaneScroll={this.onPaneScroll}
          onPaneContextMenu={this.onPaneContextMenu}
          onNodeDragStart={this.onNodeDragStart}
          onNodeDragStop={this.onNodeDragStop}
          onSelectionDragStart={this.onSelectionDragStart}
          onSelectionDrag={this.onSelectionDrag}
          onSelectionDragStop={this.onSelectionDragStop}
          onSelectionContextMenu={this.onSelectionContextMenu}
          onSelectionChange={this.onSelectionChange}
          onMoveEnd={this.onMoveEnd}
          onLoad={this.onLoad}
          connectionLineType="straight"
          nodeTypes={nodeTypes}
        >
          <Controls />
          <Background color="#ffffff22"/>
          <div
            style={{ position: "absolute", left: 20, top: 110, color: "white" }}
          >
            Cost: {this.state.answer}
          </div>
          <div
            style={{ position: "absolute", left: 20, top: 135, color: "white" }}
          >
            Path: {this.state.path}
          </div>
          <div
            style={{ position: "absolute", left: 20, top: 160, color: "white" }}
          >
            Current: {this.state.message}
          </div>
          <Button
            variant="contained"
            onClick={() => {
              this.setState({running: true}, () =>this.travellingSalesmanProblemHelper(this.state.matrix));
            }}
            style={{
              position: "absolute",
              left: 20,
              top: 60,
              zIndex: 4,
              background: "#23C779",
              color: "white",
              paddingLeft: 30,
              paddingRight: 30,
              paddingTop: 10,
              paddingBottom: 10,
              border: "none",
              borderRadius: 4,
            }}
          >
            Start
          </Button>
        <div style={{position: 'absolute', left: 20, top: 185, zIndex: 4, width: 100, color: 'white'}}>
        Delay(ms)
      </div>
      <div style={{position: 'absolute', left: 20, top: 20, zIndex: 4, fontSize: 25, fontWeight: 'bold', color: 'white'}}>
        Travelling Salesperson Visualizer
      </div>
          <Slider
          min={100}
          max={1500}
          value={this.state.delay}
          step={50}
          style={{color: '#23C779', position: 'absolute', left: 105, top: 180, zIndex: 4, width: 200}}
            onChange={(event, value) => this.setState({delay: value})}
            aria-labelledby="continuous-slider"
            valueLabelDisplay="auto"
          />
          <Button
            variant="contained"
            onClick={() => {
              this.replay();
            }}
            style={{
              position: "absolute",
              left: 135,
              top: 60,
              zIndex: 4,
              background: "#d90368",
              color: "white",
              paddingLeft: 30,
              paddingRight: 30,
              paddingTop: 10,
              paddingBottom: 10,
              border: "none",
              borderRadius: 4,
            }}
          >
            <ReplayIcon />
          </Button>
        </ReactFlow>
      </div>
    );
  }
}
