import { Handle, isEdge } from 'react-flow-renderer';
import './App.css';

const style = {zIndex: -9999, pointerEvents: 'none', opacity: 0, top: '50%'};

  const selectedStyle = {
    background: '#23C779',
    color: 'white',
    width: 50,
    height: 50,
    borderRadius: 50 / 2,
    fontFamily: 'Helvetica',
    fontWeight: 'bold',
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  }

  const answerStyle = {
    background: '#d90368',
    color: 'white',
    width: 50,
    height: 50,
    borderRadius: 50 / 2,
    fontFamily: 'Helvetica',
    fontWeight: 'bold',
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  }

  const unselectedStyle =  {
    background: 'white',
    color: '#23C779',
    width: 50,
    height: 50,
    borderRadius: 50 / 2,
    fontFamily: 'Helvetica',
    fontWeight: 'bold',
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  };

  const edgeStyle = {
    stroke: "#dddddddd",
    strokeWidth: 2,
  }

  const selectedEdgeStyle = {
    stroke: "#23C779",
    strokeWidth: 4,
  }

  const answerEdgeStyle = {
    stroke: "#d90368",
    strokeWidth: 4,
  }

  export const highlightEdgeOnSelect = (elements, selectedElements) => {
    if(elements != null) {
      const newElements = elements.map((e) => {
        if(isEdge(e)) {
          if(selectedElements.hasOwnProperty(e.id)) {
            return {...e, style: selectedEdgeStyle};
          } else {
            return {...e, style: edgeStyle};
          }
        } else {
          if(selectedElements.hasOwnProperty(e.id)) {
            return {...e, style: selectedStyle};
          } else {
            return {...e, style: unselectedStyle};
          }
        }
      });
      return newElements;
    } return elements
  }

  export const colorNodeSelect = (elements, selectedElements) => {
    if(elements != null) {
      const newElements = elements.map((e) => {
        if(isEdge(e)) {
          if(selectedElements.hasOwnProperty(e.id)) {
            return {...e, style: answerEdgeStyle};
          } else {
            return {...e, style: edgeStyle};
          }
        } else {
          if(selectedElements.hasOwnProperty(e.id)) {
            return {...e, style: answerStyle};
          } else {
            return {...e, style: unselectedStyle};
          }
        }
      });
      return newElements;
    } return elements
  }
  
  export const customNode = ({ data }) => {
    return (
      <>
      <Handle type="target" position="top" style={style} />
      <div>
            <div>{data.label}</div>
      </div>
      <Handle type="source" position="top" style={style} />
    </>
    );
  };
